.blog-info-section {
  margin: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-size: contain;
  background-position: center;
  background-repeat: repeat;
  background-image: url("../images/bg-graphic.svg");
  .blog-body {
    width: 80%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    .blog-search-input {
      font-size: 20px;
      font-weight: var(--bold);
      color: #333;
      padding: 0 10px;
      width: calc(100% - 20px);
      height: 40px;
      position: sticky;
      top: 10px;
      z-index: 999999999999999;
      border: unset;
      margin-bottom: 40px;
      outline: unset;
      box-shadow: 0px 13px 10px -7px #0000001a;
    }
    .blog-card {
      overflow: hidden;
      display: flex;
      width: 100%;
      cursor: pointer;
      background: #fdfdfd;
      height: 100px;
      margin-bottom: 20px;
      box-shadow: 0px 10px 9px -7px #0000001a;
      transition: all 0.4s cubic-bezier(0.175, 0.885, 0, 1);
      &:hover {
        box-shadow: 0px 18px 15px -8px #0000001a;
      }
      .blog-card-left-color-bar,
      .blog-card-right-color-bar {
        height: 100%;
        width: 4px;
        border-radius: 0 10px 10px 0;
        flex-shrink: 0;
      }
      .blog-card-right-color-bar {
        border-radius: 10px 0 0 10px;
      }
      .blog-card-category {
        flex-shrink: 0;
        height: 100%;
        min-width: 100px;
        padding: 0 10px;
        font-size: 16px;
        display: flex;
        align-items: center;
        justify-content: center;
        background: #f5f5f5;
      }
      h3 {
        margin: auto 0;
        width: 100%;
        line-height: unset;
        text-align: left;
        padding: 5px;
        .blog-card-date {
          color: #ccc;
          margin-top: 5px;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
      }
    }
    .blog-details-card {
      overflow: hidden;
      display: flex;
      width: 100%;
      padding: 10px;
      justify-content: space-between;
      background: #ffffff;
      height: 300px;
      margin-bottom: 20px;
      box-shadow: 0px 10px 9px -7px #0000001a;
      transition: all 0.4s cubic-bezier(0.175, 0.885, 0, 1);
      &:hover {
        box-shadow: 0px 18px 15px -8px #0000001a;
      }
      .left-part {
        position: relative;
        width: 49%;
        flex-shrink: 0;
        iframe,
        img {
          height: 100%;
          border: none;
          width: 100%;
        }

        .left-part-default-bg {
          position: absolute;
          width: 100%;
          height: 100%;
          display: flex;
          align-items: center;
          justify-content: center;
          img {
            width: 97%;
            height: 97%;
          }
        }
        .Container {
          position: absolute !important;
          z-index: 2 !important;
          height: 100% !important;
          width: 100% !important;
          border-radius: unset !important;
          border: unset !important;
          background-color: transparent !important;
          &:hover {
            background-color: unset !important;
          }
          .LowerContainer {
            background: #fff !important;
          }
        }
        .skeleton-container {
          border-radius: unset !important;
          border: unset !important;
        }
      }
      .right-part {
        width: 49%;
        flex-shrink: 0;
        display: flex;
        flex-direction: column;
        justify-content: center;
        h2 {
          min-width: 100px;
          font-size: 16px;
          text-align: left !important;
          word-break: break-word;
        }
        h3 {
          margin-top: 10px;
          text-align: left;
          line-height: unset;
          word-break: break-word;
        }
      }
    }
  }
}

@media only screen and (max-width: 800px) {
  .blog-body {
    width: 100% !important;
    .blog-card {
      height: 70px !important;
      h3 {
        display: block !important;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
    }
  }
}
