.small {
  transform: scale(00.9, 00.9);
}

.dim {
  opacity: 0.5;
}

.front-section {
  position: relative;
  height: 50vh;
  width: 100%;
}

header {
  width: calc(100% - 100px);
  padding: 10px 50px;
  // height: 50px;
  max-height: 70px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.header-content-left {
  display: flex;
  align-items: center;
}

.logo-container {
  height: 300px;
  width: 300px;
  background-size: 100% 100%;
  background-image: url("../images/logo.svg");
}

.logo-drop {
  margin: auto 20px;
  height: 50px;
  width: 2px;
  background-color: #eeeeee;
}

.name {
  cursor: pointer;
  font-size: 22px;
  font-weight: var(--bold);
  color: #fff;
  text-align: center;
}

.occ {
  margin-top: 4px;
  font-size: 15px;
  font-weight: var(--regular);
  color: #fff;
}

.header-content-right {
  cursor: pointer;
  font-size: 25px;
}

.hello {
  color: #fff;
}

.hand {
  display: inline-block;
  animation-name: wave-animation;
  animation-duration: 2s;
  animation-iteration-count: infinite;
}

.hero-content {
  position: absolute;
  top: calc(50% - 40px);
  height: 80px;
  width: 100%;
  h2 {
    margin: 0 auto;
    font-size: 25px;
    max-width: 80%;
  }
}

.repeat-text {
  margin-top: 30px;
  margin-left: -50px;
  position: absolute;
  display: inline-block;
  height: 79px;
  width: 177px;
  background-image: url("../images/repeat.svg");
}

.content-max-width {
  max-width: 1150px !important;
}

.info-section {
  margin: 150px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  background-image: url("../images/bg-graphic.svg");
}

.info-content {
  width: 100%;
  display: flex;
  align-items: center;
}

.info-content.second {
  margin-top: 50px;
}

.graphic {
  flex: 1;
  min-width: 219px;
  flex-shrink: 0;
}

.dev-expl {
  flex: 3;
}

h3.expl {
  margin-top: 15px;
}

.certified {
  display: inline-block;
  margin-top: 10px;
  padding: 2px 10px;
  font-size: 12px;
  border-radius: 5px;
  background-color: rgba(0, 0, 0, 0.05);
  font-weight: var(--bold);
}

.dev-graphic {
  height: 219px;
  width: 219px;
  background-image: url("../images/dev-graphic.svg");
}

.design-graphic {
  height: 219px;
  width: 219px;
  background-image: url("../images/design-graphic.svg");
}

.all-center-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.heart {
  cursor: pointer;
  font-size: 20px;
  display: inline-block;
  animation-name: heartbeat;
  animation-duration: 2s;
  animation-iteration-count: infinite;
}

.worked-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 30px 0 22px 0;
  background-color: rgba(0, 0, 0, 0.03);
  border-radius: 10px;
}

.worked-drop {
  margin: 15px auto;
  height: 2px;
  width: 50px;
  border-radius: 1px;
  background-color: rgba(0, 0, 0, 0.05);
}

.companies-link {
  text-align: center;
  font-size: 15px;
  font-weight: var(--regular);
  line-height: 30px;
}

.companies-link:hover {
  text-decoration: underline;
}

.reach-out-container {
  margin-top: 150px;
  display: flex;
  flex-direction: column;
}

.platforms {
  margin-top: 25px;
  display: flex;
  flex-direction: row;
}

.platform {
  cursor: pointer;
  margin: 0 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 84px;
  width: 200px;
  background-color: white;
  border-radius: 10px;
  border: 4px solid #f1f6f9;
  transition-duration: 0.25s;
}

.whatsapp:hover {
  box-shadow: 0 0 0px 2px #417a6850;
}

.email:hover {
  box-shadow: 0 0 0px 2px #a4595950;
}

.linkedin:hover {
  box-shadow: 0 0 0px 2px #596fa450;
}

.dribbble:hover {
  box-shadow: 0 0 0px 2px #a4597350;
}

.platform-text {
  text-align: left;
  font-size: 18px;
  font-weight: var(--bold);
}

.whatsapp .platform-text {
  color: #417a68;
}

.email .platform-text {
  color: #a45959;
}

.linkedin .platform-text {
  color: #596fa4;
}

.platform-icon {
  margin-right: 8px;
  height: 34px;
  width: 34px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100% 100%;
}

.whatsapp .platform-icon {
  background-image: url("../images/whatsapp.svg");
}

.email .platform-icon {
  background-image: url("../images/mail.svg");
}

.linkedin .platform-icon {
  background-image: url("../images/in.svg");
}

.dribbble .platform-icon {
  /* background-image: url("../images/dribbble.svg"); */
}

.thank-you-note {
  margin: 130px 0;
}

.thanks {
  font-size: 30px;
}

footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 150px;
  background-color: #38344a;
}

.footer-quote-text {
  color: white;
}

.footer-quote-by-text {
  font-size: 14px;
  color: #ffffff70;
}

@keyframes wave-animation {
  0% {
    transform: rotate(0deg);
  }
  10% {
    transform: rotate(24deg);
  }
  20% {
    transform: rotate(-8deg);
  }
  30% {
    transform: rotate(12deg);
  }
  40% {
    transform: rotate(-4deg);
  }
  50% {
    transform: rotate(10deg);
  }
  60% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

@keyframes heartbeat {
  0% {
    transform: scale(0.9);
  }
  20% {
    transform: scale(1.1);
  }
  40% {
    transform: scale(0.9);
  }
  60% {
    transform: scale(1.1);
  }
  80% {
    transform: scale(0.9);
  }
  100% {
    transform: scale(0.9);
  }
}
