* {
  padding: 0;
  margin: 0;
  font-family: "Nunito", sans-serif;
}

:root {
  --regular: 400;
  --bold: 700;
}

html {
  scroll-behavior: smooth;
}

body {
  background-color: #f1f6f9 !important;
}

.white {
  color: #fff;
}

.blue {
  color: #6646ed;
}

.green {
  color: #0bd8b8;
}

.red {
  color: #fb4132;
}

a {
  text-decoration: none;
}

p {
  margin: 0;
  padding: 0;
}

h1 {
  text-align: center;
  font-size: 60px;
  font-weight: var(--bold);
  color: #333;
}

h2 {
  text-align: center;
  font-size: 20px;
  font-weight: var(--bold);
  color: #333;
}

h3 {
  text-align: center;
  font-size: 15px;
  font-weight: var(--regular);
  color: #777;
  line-height: 30px;
}

h3 {
  text-align: center;
  font-size: 13px;
  font-weight: var(--regular);
  color: #777;
  line-height: 30px;
}
